.Home {
    background-color: rgb(44, 44, 44);
    min-height: 100vh;
    margin: 0px;

    display: grid;
    grid-template-areas:
        "nav head"
        "nav main"
        "foot foot";
    grid-template-rows: 67px 1fr 4em;
    grid-template-columns: 15em 1fr;
    color: white;
}

.Home > .sideBar {
    grid-area: nav;
    background: linear-gradient(
        to bottom,
        rgb(68, 10, 0), 70%, 
        rgb(0, 0, 0)
    );
    display: flex;
    flex-direction: column;
}

.functions { 
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Home > .sideBar p {
    margin: 1rem;
}

.Home > .sideBar p:hover {
    color:rgb(255, 255, 255);
}

.active {
    color: white;
}


.musicFolders {
    margin: 0 1em; 
    border-top: 1px solid rgb(150, 150, 150);
}

.Home > .head {
    display: flex;
    grid-area: head;
    background-color:rgb(182, 182, 182);
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgb(78, 78, 78)
}

.sideBar button, .head button {
    padding: 0.8em;
    margin: 1em 0;
    border-radius: 20px;
    font-weight: 700;
    background-color:rgb(255, 236, 236)
}

.head button {
    margin: 0 1em;
}

#backButton, #forwardButton {
    border-radius: 50%;
    padding: 1em;

}

.Home > .main {
    grid-area: main;
}

.Home > .footer {
    grid-area: foot;
    background-color: rgb(36, 32, 32);
    border-top: 1px solid rgb(78, 78, 78)
}

.Home button:hover {
    cursor: pointer;
    border: 2px solid rgb(201, 70, 70);
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;         /* Opera/IE 8+ */
}

.AddFolder {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
}

.overlay {
    position: fixed;
    background-color: black;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}